import React, { useState } from 'react'
import {Route} from 'react-router-dom'


import Portfolio from './Portfolio/Portfolio'
import Article from './Article/Article'

import MainPage from './MainPage/MainPage'
import Message from './Message/Message'
import StaredArticles from './StaredArticles/StaredArticles'


const Main = () => {

	const [stared, setStared] = useState({
		1:true,
		3:true,
	})

	const addStar = (id) => {
		setStared((value) => ({
			...value,[id]:true
		}))
	}
	const removeStar = (id) => {
		setStared((value) => ({
			...value,[id]:false
		}))
	}

	return (
		<main className="main">
			<Route path="/" exact component={MainPage}/>
			<Route path="/portfolio" render={() => (
				<Portfolio
					stared={stared}
					addStar={addStar}
					removeStar={removeStar}
				/>
			)}/>
			<Route path="/staredArticles" render={() => (
				<StaredArticles
					stared={stared}
					addStar={addStar}
					removeStar={removeStar}
				/>
			)}/>
			<Route path="/article/:id" render={({match}) => {
					return (
						<Article
							match={match}
							stared={stared[match.params.id]}
							addStar={addStar}
							removeStar={removeStar}
						/>
					)
				}
			}/>
			<Message/>
		</main>
	)
}

export default Main