import React from 'react'

const MessageForm = () => {
	return (
		<div className="message__form">
			<form action="/">
				<h3 className="message__title">Leave a Message</h3>
				<input type="text" placeholder="Name" name="name" defaultValue=""/>
				<input type="text" placeholder="Email" name="email" defaultValue=""/>
				<textarea name="message" placeholder="Message"></textarea>
				<button className="btn message__btn" type="button">Hire Me!</button>
			</form>
		</div>
	)
}

export default MessageForm