import React from 'react'

import MessageContactInfo from "./MessageContactInfo"

const MessageContact = () => {
	function createMarkup(img) {
		return {__html: img};
	 }
	return (
		<div className="message__contact contact">
			{
				MessageContactInfo.map(({id,img,title,infoText}) => (
					<div className="contact__item" key={id}>
						<div className="contact__image" dangerouslySetInnerHTML={createMarkup(img)}/>
						<div className="contact__info">
							<h4 className="contact__info-title">{title}</h4>
							<p className="contact__info-text">
								{infoText}
							</p>
						</div>
					</div>
				))
			}
		</div>
	)
}

export default MessageContact