import React, {Component} from 'react'

import LifeItem from './LifeItem/LifeItem'
import middle from '../../../../../images/life/midlle.svg'

class Life extends Component{

	render() {
		const {
			title,
			items,
		} = this.props
		return (
			<section className="life">
				<span className="block-line"></span>
				<div className="container">
					<h2 className="block-title2 life__title">{title}</h2>
					<div className="life__body">
						<div className="life__item-middle">
							<img src={middle} alt=""/>
						</div>
						{
							items.map((item,index) => {
								let classNames;
								if (index % 2 === 0) {
									classNames = `life__item life__item${index+1} life__item-left`;
								} else {
									classNames = `life__item life__item${index+1} life__item-right`;
								}
								return (
									<LifeItem
										key={item.id}
										classNames={classNames}
										profName={item.profName}
										tag={item.tag}
										date={item.date}
										text={item.text}
									/>
								)
							})
						}
					</div>
				</div>
			</section>
		)
	}
}

export default Life