import React from 'react'

import Hire from './Hire/Hire'
import About from './About/About'
import Life from './Life/Life'
import LifeContent from './Life/LifeContent'
import Skills from './Skills/Skills'


const MainPage = () => {
	return (
		<>
			<Hire/>
			<About/>
			{
				LifeContent.map(({
					id,
					title,
					items,
				}) => (
					<Life
						key={id}
						title={title}
						items={items}
					/>
				))
			}
			<Skills/>
		</>
	)
}

export default MainPage