import React from 'react'

const Hire = () => {
	return (
		<section className="hire">
			<div className="hire__body">
				<h1 className="hire__title">Lets Make Something Great</h1>
				<p className="hire__text">
					I’m an UI designer that prides ourself on being strategic brand partners and awesome product providers
				</p>
				<a href="/" className="btn hire__btn">Hire Me!</a>
			</div>
		</section>
	)
}

export default Hire