import React from 'react'

const LifeItem = ({
	classNames,
	profName,
	tag,
	date,
	text,
}) => {
	return (
		<div className={classNames}>
			<span className="life__prof-name">{profName}</span>
			<span className="life__tag">{tag}</span>
			<p className="life__date">{date}</p>
			<p className="life__text">{text}</p>
		</div>
	)
}

export default LifeItem