import React, {Component} from 'react'
import {HashRouter} from 'react-router-dom'

import Header from './Header/Header'
import Wrapper from './Wrapper/Wrapper'
import './nullstyle.css'
import './style.css'

class App extends Component {

	toggleClass = (el1,el2,el3) => {
		let elem1 = document.querySelector(el1);
		let elem2 = document.querySelector(el2);
		let elem3 = document.querySelector(el3);

		elem1.classList.toggle('open');
		elem2.classList.toggle('open');
		elem3.classList.toggle('move');
		elem3.classList.toggle('lock');
	}

	render() {
		return (
			<>
				<HashRouter>
					<button onClick={() => this.toggleClass('.button','.header','.wrapper')} className="button" type="button"><span></span></button>
					<Header
						toggleClass={this.toggleClass}
					/>
					<Wrapper/>
				</HashRouter>
			</>
		)
	}
}

export default App