import React from 'react'

import PortfolioContent from "./PortfolioContent"
import PortfolioItem from './PortfolioItem/PortfolioItem'

const Porfolio = ({
	stared,
	removeStar,
	addStar,
}) => {

	let buttons = [];
	const getButtons = () => {
		buttons = document.querySelectorAll(".cats__item");
	}
	let portfolioItems = [];
	const getPortfolioItems = () => {
			portfolioItems = document.querySelectorAll(".portfolio__item");
	}

	const setClass = (el) => {
		el.classList.add("active");
	}
	const romoveClass = (el) => {
		el.classList.remove("active");
	}

	const showHideElement = (arrElem,target) => {
		arrElem.forEach((item) => {
			item.style = "display: block;"
			if(target.textContent === "ALL" || target.textContent === "All") {
				item.style = "display: block;"
			} else if(target.textContent !== item.getAttribute("cat")) {
				item.style = "display: none;"
			}
		})
	}

	const clickCategory = (event) => {
		getButtons();
		getPortfolioItems();

		buttons.forEach((button) => {
			if(button.classList.contains("active")) {
				romoveClass(button)
			}
		})

		let target = event.target;
		setClass(target);

		showHideElement(portfolioItems,target)
	}

	return (
		<section className="portfolio">
			<div className="container">
				<h2 className="block-title2 portfolio__title">Portfolio</h2>
				<div className="portfolio__cats cats">
					<button className="cats__item active" type="button" onClick={(e) => clickCategory(e)}>All</button>
					<button className="cats__item" type="button" onClick={(e) => clickCategory(e)}>Web Design</button>
					<button className="cats__item" type="button" onClick={(e) => clickCategory(e)}>Animation</button>
					<button className="cats__item" type="button" onClick={(e) => clickCategory(e)}>UI/UX</button>
					<span className="cats__underline"></span>
				</div>
				<div className="portfolio__items">
					{
						PortfolioContent.map(({
							id,
							title,
							cat,
							category,
							img,
						}) => {
							return (
								<PortfolioItem
									key={id}
									id={id}
									title={title}
									cat={cat}
									category={category}
									img={img}
									clickCategory={clickCategory}
									stared={stared[id]}
									removeStar={removeStar}
									addStar={addStar}
								/>
							)
						})
					}
				</div>
			</div>
		</section>
	)
}

export default Porfolio