import React from 'react'
import {Link} from 'react-router-dom'

import devphoto from '../../images/dev-photo.jpg'

const Header = ({
	toggleClass,
}) => {
	return (
		<header className="header">
			<div className="header__img">
				<img src={devphoto} alt="Developer"/>
				<div className="header__dev-info">
					<p className="header__dev-name">Al Rayhan</p>
					<p className="header__dev-prof">UI/UX Expert</p>
				</div>
			</div>
			<ul className="header__menu menu">
				<li className="menu__item">
					<Link onClick={() => {
						if(window.innerWidth <= 910) {
							toggleClass('.button','.header','.wrapper')
						}
					}} to="/" className="menu__link">Home</Link>
				</li>
				<li className="menu__item">
					<a href="#" className="menu__link">About Me</a>
				</li>
				<li className="menu__item">
					<a href="#" className="menu__link">Education</a>
				</li>
				<li className="menu__item">
					<a href="#" className="menu__link">Work Experience
					</a>
				</li>
				<li className="menu__item">
					<a href="#" className="menu__link">Skills</a>
				</li>
				<li className="menu__item">
					<Link onClick={() => {
						if(window.innerWidth <= 910) {
							toggleClass('.button','.header','.wrapper')
						}
					}} to="/portfolio" className="menu__link">Portfolio</Link>
				</li>
				<li className="menu__item">
					<Link onClick={() => {
						if(window.innerWidth <= 910) {
							toggleClass('.button','.header','.wrapper')
						}
					}} to="/staredArticles" className="menu__link">Stared articles</Link>
				</li>
				<li className="menu__item">
					<a href="/" className="menu__link">Contacts</a>
				</li>
			</ul>
		</header>
	)
}

export default Header