import React from 'react'
import { Link } from 'react-router-dom'

import arrow from '../portfolio/arrow.png'
import star from '../../../../../images/star.svg'
import star2 from '../../../../../images/star2.svg'

const PortfolioItem = ({
	id,
	title,
	cat,
	category,
	img,
	clickCategory,
	stared,
	removeStar,
	addStar,
}) => {
	return (
		<>
			<div className="portfolio__item card" cat={cat}>
				<img className="card__img" src={img} alt=""/>
				<div className="card__info">
					<h3 className="card__title">{title}</h3>
					<button className="card__star"
					onClick={() => stared ? removeStar(id) : addStar(id)}
					>
						{
							stared ? <img src={star2} alt="star"/> : <img src={star} alt="star"/>
						}
					</button>
					<button onClick={(e) => clickCategory(e)} cat={cat} className="card__cat">{category}</button>
					<Link to={`/article/${id}`} className="card__btn">View Article
						<img src={arrow} alt=""/>
					</Link>
				</div>
			</div>
		</>
	)
}

export default PortfolioItem