import React from 'react'

import PortfolioContent , {getArticlesObject} from '../Portfolio/PortfolioContent'
import star from '../../../../images/star.svg'
import star2 from '../../../../images/star2.svg'

const Article = ({
	match,
	articlesObject = getArticlesObject(PortfolioContent),
	stared,
	removeStar,
	addStar,
}) => {
	const id = match.params.id;
	return (
		<article className="article">
			<div className="container">
				<div className="article__img">
					<img src={articlesObject[id].img} alt=""/>
					<div className="article__info">
						<button className="card__star"
						onClick={() => stared ? removeStar(id) : addStar(id)}
						>
							{
								stared ? <img src={star2} alt="star"/> : <img src={star} alt="star"/>
							}
						</button>
						<h3 className="article__info-title">{articlesObject[id].title}</h3>
						<p className="article__info-cat">
						{articlesObject[id].category}
						</p>
					</div>
				</div>
				<div className="article__text" 
						dangerouslySetInnerHTML={{
							__html:articlesObject[id].text
					}}></div>
			</div>
		</article>
	)
}

export default Article