import React from 'react'

import aboutPhoto from '../../../../../images/about/penguin.webp'

const About = () => {
	return (
		<section className="about">
			<div className="container">
				<div className="about__row">
					<div className="about__info">
						<h2 className="block-title2 about__title">About Me</h2>
						<div className="block-text about__text">
							<p>
								(Цей блок зроблений на display: grid) Lorem ipsum dolor sit amet, ectetuer nonummy nibh euismod tincidunt ut laoreet  magna  laoreet  aliquam erat volutpat.
							</p>
						</div>
						<div className="about__char char">
							<p className="char__item">Nmae: Al Rayhan</p>
							<p className="char__item">Age: 23 years</p>
							<p className="char__item">Email: mail@deviserweb.com</p>
							<p className="char__item">Web: deviserweb.com</p>
							<p className="char__item">Address: 24, Golden Street, New Castle</p>
						</div>
					</div>
					<div className="about__image">
						<img src={aboutPhoto} alt="penguin"/>
					</div>
				</div>
			</div>
		</section>
	)
}

export default About