import React from 'react'

import PortfolioContent from '../Portfolio/PortfolioContent'
import PortfolioItem from '../Portfolio/PortfolioItem/PortfolioItem'

const StaredArticles = ({
	stared,
	removeStar,
	addStar,
}) => {
	return (
		<section className="portfolio">
			<div className="container">
				<h2 className="block-title2 portfolio__title">Stared articles</h2>
				<div className="portfolio__items">
					{
						PortfolioContent.map(({
							id,
							title,
							cat,
							category,
							img,
						}) => {
							if(stared.hasOwnProperty(id) && stared[id]) {
								return (
									<PortfolioItem
										key={id}
										id={id}
										title={title}
										cat={cat}
										category={category}
										img={img}
										stared={stared[id]}
										removeStar={removeStar}
										addStar={addStar}
									/>
								)
							} else {
								return null;
							}
						})
					}
				</div>
			</div>
		</section>
	)
}

export default StaredArticles